// 頁籤nav tabs
.custom-nav-tabs {
    .nav-link {
        border-radius: 0px;
        background-color: $gray-300;
        border-color: $gray-300;
        border-bottom: 6px solid $gray-300;
        margin-bottom: 0;
        &.active {
            background-color: $main-color01;
            border-color: $main-color01;
            border-bottom: 6px solid $main-color01-dark;
        }
        &.secondary.active {
            background-color: $main-color04;
            border-color: $main-color04;
            border-bottom: none;
        }
    }
    border-bottom: 0;
    button {
        h6 {
            color: $gray-600;
        }
        &.active {
            h6 {
                color: $white;
            }
        }
    }
}
