// ===== 樣式(共用) =====
body {
    font-family: "Noto Sans TC", sans-serif;
    background-color: $gray-200;
    position: relative;
    z-index: -2;
    overflow-x: hidden;
}

main {
    min-height: 100dvh;
}
.table > :not(caption) > * > * {
    background-color: transparent;
    @include media-breakpoint-down(sm) {
        padding: 0;
    }
}
.tooltip-inner {
    text-align: left !important;
}

// 驗證必填提示字
.valid-error {
    color: $red;
    font-size: 1rem;
}

// 驗證共用
.field-validation-error {
    color: #dc3545;
    margin-bottom: 0.5rem;
    display: inline-block;
}

// 連結
.link-color {
    color: $main-color03;
}

// 標題共用
.page-title {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    background-color: $white;
    border-left: 5px solid $main-color01;
    border-radius: $custom-border-radius;
    padding: 0.75rem 1rem;
    margin: 1rem 0;
    box-shadow: $custom-box-shadow;
    border-radius: $custom-border-radius;
    h5 {
        color: $main-color01-dark;
        font-weight: bold;
        font-size: 1.5rem;
        margin: 0;
    }
    h6 {
        color: $main-color01;
        font-weight: bold;
        display: inline-block;
        margin-bottom: 0;
    }
}

// 分頁 pagination
.list-pagination {
    text-align: center;
    select {
        width: auto;
        display: inline;
    }
}

.ui-datepicker-trigger {
    position: absolute !important;
    top: 10px !important;
    right: 24px !important;
}

// 標題clipboard
.page-title-clipboard {
    &::before {
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        content: "\f328";
        font-size: 1rem;
    }
}

// info區
.info-massage {
    position: relative;
    background: #fcedbb;
    border-left: #efdea5 solid 6px;

    .watermark {
        position: absolute;
        right: 20px;
        bottom: 15px;
        width: 50px;
    }
}

// Badges狀態
.status-btn {
    display: inline-block;
    padding: 0.25rem 0.5rem;
    font-size: 75%;
    font-weight: 700;
    line-height: 1;
    text-align: center;
    white-space: nowrap;
    vertical-align: baseline;
    border-radius: 0.25rem;
    color: $white;
    // 完成
    &.success {
        background-color: #509936;
    }
    // 送出
    &.send {
        background-color: $main-color04;
    }
    // 進行中
    &.pending {
        background-color: $gray-600;
    }
}

// 表格查無資料
.table-nodata {
    display: table-caption !important;
    text-align: center;
    caption-side: bottom;
    margin: 0 auto;
}

// 圖片箱子---目前未使用---------------------------------------------
.img-upload {
    background-color: #f3f3f3;
    width: 100%;
    height: 240px;
    border: 1px dashed $gray-600;

    @include media-breakpoint-down(md) {
        height: 180px;
    }
    @include media-breakpoint-only(md) {
        height: 140px;
    }
}
.img-area {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    color: $gray-200;
    cursor: pointer;
    .img {
        width: 100%;
        height: 100%;
        background: center / cover no-repeat;
    }
    .delete-btn {
        z-index: 1;
        color: $white;
        text-align: center;
        background: rgba(0, 0, 0, 0.5);
        bottom: 0;
        right: 0;
        height: 40px;
        display: block;
        position: absolute;
        width: 100%;
        line-height: 2.4;
    }
}
