@import "../../../node_modules/bootstrap/scss/variables";
@import "../../../node_modules/bootstrap/scss/variables-dark";

$text-color: #252525;

$main-color01: #106b57; //主色
$main-color02: #ef9360; //輔助色
$main-color03: #087085; //輔助色
$main-color04: #e8cc3c; //強調色

$main-color01-light: #eaf3ed;
$main-color02-light: #ffe4d4;
$main-color01-dark: #1e322f;

$custom-theme: (
    "color01": $main-color01,
    "color02": $main-color02,
    "color03": $main-color03,
    "color04": $main-color04,
    "color01-light": $main-color01-light,
    "color02-light": $main-color02-light,
    "color01-dark": $main-color01-dark,
);

$button-colors: (
    "yellow": $main-color04,
    "orange": #f67322,
    "red": #ce1235,
);

$custom-box-shadow: 0px 4px 17px 0px rgba(156, 161, 186, 0.25);
$custom-border-radius: 0.5rem;
