.navbar {
    &.top-nav {
        background-color: $main-color01-dark;
        color: white;
        height: 30px;
        padding: 0 1rem;
        margin: 0 auto;
        a {
            color: white;
            &:hover {
                color: $main-color01-light;
                transition: 0.3s;
            }
        }
    }
    &.second-nav {
        background-color: rgba(255, 255, 255, 0.96);
        padding: 0 1rem;
    }
    .navbar-brand {
        padding-top: 0.5rem;
        padding-bottom: 0.5rem;
        font-size: 1.5rem;
        &:hover {
            color: #263347;
        }
        .main-title {
            font-size: 1.2rem;
            font-weight: 600;
            color: $main-color01-dark;
        }
        .sub-title {
            font-size: 1rem;
            color: #32373b;
            line-height: 1.5;
        }
    }
    .navbar-collapse {
        justify-content: flex-end;
    }
    .navbar-nav {
        gap: 2.5rem;
        @include media-breakpoint-down(lg) {
            margin-bottom: 1rem;
        }
        .nav-link {
            font-size: 1.1rem;
            color: $main-color01-dark;
            &:hover {
                color: darken($main-color01-dark, 15%);
            }
            &.active {
                border-bottom: 3.6px solid $main-color01;
            }
        }
        .dropdown-item {
            padding: 0.5rem 1rem;
        }
        .dropdown-item.active,
        .dropdown-item:active {
            background-color: #e9ecef;
            color: #32373b;
            font-weight: 500;
            border-bottom: none;
        }
    }
    // 漢堡
    .navbar-toggler {
        outline: none;
        border: 0;
    }
    .navbar-toggler:focus {
        outline: none;
        box-shadow: none;
    }
    .dropdown-menu {
        z-index: 1040 !important;
    }
}
