//列表共用
.list-area {
    .table {
        width: 100%;
        display: table;
        @include media-breakpoint-down(sm) {
            border-top: 3px solid $main-color01;
            padding-top: 0;
        }
        .row {
            display: table-row;
            width: 100%;
            @include media-breakpoint-down(sm) {
                padding: 0.25rem 0;
                display: block;
                border-bottom: 1px solid #dee2e6;
                margin-inline: auto;
                &:last-child {
                    border: none;
                }
            }
            &.list-header {
                font-weight: bold;
                background-color: $main-color01-light;
                cursor: pointer;
                @include media-breakpoint-down(sm) {
                    display: none;
                }
                .cell {
                    padding: 0.25rem 0.5rem;
                }
            }
            &.list-body {
                &:nth-child(2n + 1) {
                    background-color: $gray-100;
                }
                &:hover {
                    background-color: #fffdeb;
                }
            }
            .cell {
                padding: 0.5rem;
                display: table-cell;
                vertical-align: middle;
                border-bottom: 1px solid $gray-300;
                width: auto;
                min-width: 5rem;
                & > div > input[type="checkbox"] {
                    margin: 0;
                }
                &:first-child {
                    width: fit-content;
                    min-width: unset;
                    & > div {
                        width: fit-content;
                        margin-inline: auto;
                        padding: 0;
                    }
                }
                button {
                    padding: 0.25rem 0.5rem;
                }
                @include media-breakpoint-down(sm) {
                    padding: 0.5rem;
                    display: flex;
                    border-bottom: none;
                    align-items: baseline;
                    &:before {
                        content: attr(data-title);
                        margin-right: 0.5rem;
                        font-weight: bold;
                        color: $main-color01;
                        display: inline-block;
                        width: 5rem;
                    }
                }
            }
        }
    }
}

//編輯區塊
.edit-area {
    background-color: $white;
    .area-title {
        padding: 0.75rem 1rem;
        margin-bottom: 0;
        color: $white;
        background-color: $main-color01;
        border-left: $main-color01-dark solid 6.3px;
    }

    .list-area {
        @include media-breakpoint-down(sm) {
            border-top: none;
        }
    }

    .edit-list-btn {
        border-bottom: 1.5px solid $gray-200;
        padding-top: 0.375rem;
        padding-bottom: 0.475rem;
        margin-bottom: 0.2rem;

        a,
        button {
            vertical-align: middle;
        }
    }
}
//table
table {
    width: 100%;
    th {
        background-color: $main-color01-light;
        padding: 10px;
        color: #364848;
        border-bottom: 1px solid #d8d8d8;
        width: auto;
        font-weight: bold;
    }
    td {
        padding: 10px 5px;
        border-bottom: 1px solid #d8d8d8;
    }
    tbody tr {
        &:nth-child(2n) {
            background-color: $gray-100;
        }
        &:hover {
            background-color: #fffdeb;
        }
    }
    @include media-breakpoint-down(md) {
        border-top: 2px solid $main-color01;
        tr {
            display: block;
            border-bottom: 1px solid #d8d8d8;
        }
        th {
            display: none;
        }
        td {
            padding: 0;
            line-height: 2em;
            display: flex;
            align-items: center;
            border: none;
            gap: 1rem;
            &:last-child {
                &::before {
                    padding: 1.25rem 0;
                }
            }
            &::before {
                content: attr(data-th);
                width: 5rem;
                padding: 0.5rem 0;
                background-color: transparent;
                color: $main-color01;
                font-size: 16px;
                font-weight: 700;
                line-height: normal;
            }
        }
    }
}
