// 側邊欄-登記證
.status-card {
    background-color: $white;
    position: relative;
    .watermark {
        position: absolute;
        right: 20px;
        top: 50px;
        width: 45px;
        @include media-breakpoint-down(lg) {
            display: none;
        }
    }
    // 已通過
    &.status-success {
        .sidebar-title {
            padding: 0.5rem 1rem;
            background-color: #5ab74f;
            color: $white;

            a {
                color: $white;
            }
        }
    }
    // 待審核
    &.status-verify {
        .sidebar-title {
            padding: 0.5rem 1rem;
            background-color: gray;
            color: $white;

            a {
                color: $white;
            }
        }
    }
    // 不通過
    &.status-failed {
        .sidebar-title {
            padding: 0.5rem 1rem;
            background-color: #cb0029;
            color: $white;

            a {
                color: $white;
            }
        }
    }
    // 限期補正
    &.status-deadline {
        .sidebar-title {
            padding: 0.5rem 1rem;
            background-color: rgb(255, 157, 0);
            color: $white;

            a {
                color: $white;
            }
        }
    }
    // 登記證內容
    &-detail {
        padding: 0.5rem 1rem;
        word-break: break-all;
    }
}
