//  ===== 管理看板index.html =====
.index {
    &-content {
        padding: 0.75rem 1rem;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 0.5rem 1rem;
        background-color: #fff;
        @include media-breakpoint-down(md) {
            grid-template-columns: 1fr;
        }
    }
}

.status-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 0;
    color: $gray-600;
    border-bottom: 1px dotted $gray-500;

    &:hover {
        cursor: pointer;
    }

    a {
        color: $gray-600;

        &:hover {
            color: $main-color03;
            text-decoration: none;
        }
    }

    .date {
        padding: 0 0.5rem;
        border-radius: 50rem;
        line-height: 1.5;
        background-color: lighten($main-color02-light, 25%);
    }
}

// 計畫表格
.data-items {
    .links {
        position: relative;
    }

    .links-a {
        @include media-breakpoint-down(sm) {
            position: absolute;
            top: 10px;
            right: 18px;
        }
    }

    .list-header {
        background-color: #e9ecef;
    }

    .cell {
        text-align: center;

        &:first-child {
            text-align: left;
        }

        @include media-breakpoint-down(sm) {
            text-align: left;
        }

        // 折線圖-圖示
        i.teal {
            color: $teal;
        }
        i.cyan {
            color: $cyan;
        }
    }
}

// 計畫表格-進度條
.progress {
    height: 0.5rem;
    border-radius: 10rem;
    padding: initial !important;
    background-color: #e9ecef !important;
    &-bar {
        background-color: $main-color04 !important;
    }
}

// 資料取得時間
.data-time {
    color: $gray-700;

    span {
        color: $gray-600;
    }
}

//  ===== 側邊欄區域 =====

// 主要側邊欄
.app-form-sidebar {
    a {
        color: #2e2e2e;

        &:hover {
            text-decoration: none;
            color: lighten(#2e2e2e, 5%);
        }
    }
    // 狀態確認/錯誤
    .status-check {
        color: #9b9b9b;

        &.success {
            color: #509936;
        }

        &.error {
            color: #d42323;
        }
    }
    // 狀態圈圈
    .status-circle {
        display: inline-block;
        width: 8px;
        height: 8px;
        margin-bottom: 2px;
        margin-right: 0.5rem;
        background: #919191;
        border-radius: 50%;

        &.success {
            background: #509936;
        }

        &.error {
            background: #d42323;
        }
    }
    // 計畫名稱
    .sidebar-title {
        background-color: $main-color01;
        color: white;
    }

    .title {
        background-color: white;
        font-size: 1.12rem;
        padding: 0.5rem 1rem;

        @include media-breakpoint-only(md) {
            font-size: 1rem;
        }

        &:hover {
            color: #2e2e2e;
            background-color: darken(#f8f8f8, 1%);
        }

        &.active {
            background-color: #d1d1d1;

            a {
                font-weight: 500;
            }
        }
    }
    // 紀錄單號
    .record-number {
        @include media-breakpoint-up(md) {
            font-size: 0.875rem;
        }
        @include media-breakpoint-up(lg) {
            font-size: 1rem;
            margin-left: 0.5rem;
        }
        @include media-breakpoint-down(sm) {
            display: inline-block;
            margin-left: 0.5rem;
        }
    }
    // 內容
    .content {
        background-color: #f8f8f8;
        font-size: 1.12rem;
        padding: 0.5rem 1rem 0.5rem 3rem;
        &:hover {
            color: #2e2e2e;
            background-color: darken(#f8f8f8, 1%);
        }

        &.active {
            background-color: #d1d1d1;

            a {
                font-weight: 500;
            }
        }
    }
}
