//表單基本資料
.form-area {
    .form-group {
        margin: 0;
        padding: 5px 0;

        > label {
            background-color: $gray-200;
            border-left: 2px solid $main-color01;
            text-align: right;
            padding-right: 0.5rem;
            padding-left: 0.25rem;
        }

        .form-control-plaintext {
            line-height: 1.2;
        }

        .dropdown_unit {
            > div {
                width: 33%;
            }
        }
    }
}

//資料維護區塊1(個人資料)
.form-area1 {
    background-color: $white;
    .row {
        margin: 0;
    }
    .col-form-label {
        background-color: #dee2e6;
        border-left: 2px solid $main-color01;
        padding-right: 0.5rem;
        padding-left: 0.25rem;
        text-align: right;
    }
    .custom-radio {
        padding-top: 7.5px;
    }
}

//資料維護區塊2
.form-area2 {
    background-color: $white;

    h6 {
        font-weight: bold;
        background-color: $main-color01;
        color: $white;
        padding: 0.75rem 1rem;
        border-left: 4px solid $main-color01-dark;
    }

    label {
        padding-right: 0.05rem;
        font-weight: bold;

        @include media-breakpoint-up(md) {
            text-align: right;
        }
    }

    .custom-checkbox {
        padding-top: 0.375rem;
        padding-bottom: 0.375rem;
    }
}
