@font-face {
    font-family: "Noto Sans TC";
    src:
        local("NotoSansTC-Light"),
        //先抓使用者電腦裡的字體
        local("Noto Sans TC Light"),
        url("../fonts/NotoSansTC-Light.ttf") format("truetype");
    font-weight: lighter;
    font-style: normal;
}

@font-face {
    font-family: "Noto Sans TC";
    src:
        local("NotoSansTC-Regular"),
        local("Noto Sans TC Regular"),
        url("../fonts/NotoSansTC-Regular.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "Noto Sans TC";
    src:
        local("NotoSansTC-Medium"),
        local("Noto Sans TC Medium"),
        url("../fonts/NotoSansTC-Medium.ttf") format("truetype");
