    font-style: normal;
}

@font-face {
    font-family: "Noto Sans TC";
    src:
        local("NotoSansTC-Bold"),
        local("Noto Sans TC Bold"),
        url("../fonts/NotoSansTC-Bold.ttf") format("truetype");
